h2 {
  font-size: 2.08vw;
  margin: 0;
  line-height: 1.664vw;
  font-weight: 400;
}

button {
  background: transparent;
  margin: 0;
  padding: 0;
  border: transparent;
}

.section {
  width: 100%;
  min-height: 100vh;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  float: left;
  padding-top: 3.646vw;
  padding-bottom: 4.01vw;
  position: absolute;
  top: 0;
  left: 0;
  @media screen and (max-width: $mobileBreakPoint) {
    padding-top: 26.667vw;
    padding-bottom: 8vw;
  }
  &.root {
    height: auto;
    padding-top: 0;
    padding-bottom: 0;
    position: relative;
  }
  .bg-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .nav {
    width: 100%;
    display: flex;
    position: fixed;
    &.top {
      padding: 2.86vw 5.2vw 0 5.2vw;
      top: 0;
    }
    &.bottom {
      padding: 0 5.2vw 3.12vw 5.2vw;
      align-items: center;
      bottom: 0;
      @media screen and (max-width: $mobileBreakPoint) {
        padding-bottom: 10.667vw;
      }
    }
    &.only-mobile {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5.333vw;
      top: 8vw;
      > div {
        display: flex;
        align-items: center;
        .nav-button {
          .icn {
            margin: 0;
          }
          &.music {
            margin-left: 4vw;
            .icn {
              max-width: 12.533vw;
              max-height: 10.933vw;
            }
          }
          &.cart {
            .icn {
              max-width: 15.2vw;
              max-height: 13.333vw;
            }
          }
        }
        &.left {
          .lang-switch {
            span {
              font-size: 5.333vw;
              &.active {
                display: none;
              }
            }
          }
        }
      }
    }
    .nav-button {
      cursor: pointer;
      display: flex;
      align-items: center;
      .icn {
        max-width: 1.56vw;
        max-height: 1.716vw;
        margin-right: 0.52vw;
      }
      span {
        font-size: 1.352vw;
        line-height: 1.508vw;
        color: $black;
      }
      &.checkout {
        margin-right: 0;
        margin-left: auto;
      }
      &.sound {
        margin-left: 0;
        margin-right: auto;
      }
      &.phone {
        margin-left: auto;
        margin-right: 2.34vw;
      }
      &.lang-switch {
        margin-left: 0;
        margin-right: 0;
        span {
          &:first-child {
            margin-right: 0.26vw;
          }
          &.active {
            text-decoration: underline;
          }
        }
      }
      &.copy {
        margin-left: auto;
        margin-right: auto;
        font-size: 0.833vw;
        cursor: default;
        @media screen and (max-width: $mobileBreakPoint) {
          font-size: 3.733vw;
        }
        span {
          font-size: 0.833vw;
          text-decoration: underline;
          margin-left: 0.26vw;
          @media screen and (max-width: $mobileBreakPoint) {
            font-size: 3.733vw;
            margin-left: 1.333vw;
          }
        }
      }
    }
  }
}

#trail-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

