    width: 12.177vw;
    left: 22.323vw;
    bottom: 2.146vw;
    @media screen and (max-width: $mobileBreakPoint) {
      width: 50.333vw;
      bottom: auto;
      left: auto;
      top: 150.467vw;
      right: 0;
    }
  }
  #cat-mobile-bg {
    position: absolute;
    width: 40vw;
    left: 34.667vw;
    bottom: 10vw;
  }
}

.cup-wrp {
  width: 18.542vw;
  height: 31.25vw;
  .cup-model-wrp {
    width: 18.542vw;
    height: 31.25vw;
    position: fixed;
    transition: all 0.5s linear;
    cursor: move;
    @media screen and (max-width: $mobileBreakPoint) {
      width: 67.467vw;
      height: 113.333vw;
    }
  }

  .rotate-hint {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    display: flex;
    justify-content: center;
    img {
      width: 2.083vw;
    }
    @media screen and (max-width: $mobileBreakPoint) {
      display: none;
    }
  }

  @media screen and (max-width: $mobileBreakPoint) {
    width: 67.467vw;
    height: 113.333vw;
  }
  .cup-img {
    width: 100%;
    object-fit: contain;
    display: none;
  }
}

.page-wrapper {
  height: 100%;
  width: 100%;
  padding: 1.56vw 5.2vw;
  display: flex;
  flex-direction: column;
  float: left;
  overflow: hidden;
  @media screen and (max-width: $mobileBreakPoint) {
    padding: 0 0 0 5.333vw;
  }

  .header {
    width: 100%;
    height: auto;
    display: flex;
    align-items: flex-end;
    .back-btn {
      font-size: 1.352vw;
      cursor: pointer;
      @media screen and (max-width: $mobileBreakPoint) {
        font-size: 6.933vw;
      }
    }
    h2 {
      margin-left: 7.813vw;
      @media screen and (max-width: $mobileBreakPoint) {
        margin-left: 0;
      }
    }
  }
  .content {
    width: 100%;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 1.458vw 0 0 0;
    @media screen and (max-width: $mobileBreakPoint) {
      padding: 0;
      flex-direction: column-reverse;
      justify-content: flex-end;
      align-items: flex-end;
    }
    .for-content {
      width: 100%;
      padding-right: 5.2vw;
      @media screen and(max-width:$mobileBreakPoint) {
        padding-right: 0;
      }
      //demo purpose
      img {
        width: 100%;
      }
    }
    .for-cup {
      width: 18.542vw;
      height: 31.25vw;
      flex-shrink: 0;
      @media screen and (max-width: $mobileBreakPoint) {
        width: 27.2vw;
        height: 45.867vw;
        margin-bottom: 5.6vw;
        margin-right: 5.333vw;
        display: none;
      }
      .cup-wrp {
        width: 100%;
        height: 100%;
      }
    }
  }
  &.righted {
    padding-right: 0;
    .header {
      h2 {
        margin-left: 19.219vw;
      }
    }
    .content {
      flex-direction: row-reverse;
      @media screen and (max-width: $mobileBreakPoint) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding-top: 5.333vw;
      }
      .for-content {
        padding-left: 7.813vw;
        padding-right: 0;
        @media screen and (max-width: $mobileBreakPoint) {
          padding-left: 0;
        }
      }
    }
  }
}

.only {
  &-mobile {
    @media screen and (min-width: $mobileBreakPoint + 1) {
      display: none;
    }
  }
  &-desktop {
    @media screen and (max-width: $mobileBreakPoint) {
      display: none;
    }
  }
}
